html {
    background-color: #ffffff;
    min-width: 600px;
    --card-width: 813px;
    --card-height: 1185px;
    --global-scale: 1;
    --resize-ratio: 0.7;
    --resize-percentage: 70%;

    --color-contrast: #3b3b3b;
    --color-dim: #9b9b9b;
    --color: #d3d3d6;
    --color-heavy: #eeeeee;
    --color-vendor: #d0f0ff;

    --main-disabled: #ffffff66;

    --main-level-5: #505050;
    --sub-level-5: #606060;
    --main-level-4: #5b626e;
    --sub-level-4: #707681;
    --main-level-3: #4e4e53;
    --sub-level-3: #68686b;
    --main-level-2: #fafafa;
    --sub-level-2: #dfdfdf;
    --main-level-1: #333333;
    --main-level-1-active: #333333;
    --sub-level-1: #2a2a2a;

    --focus: #70d9ff;
    --main-link: #27a9e6;
    --sub-link: #2badea;
    --main-active: #1890ff;
    --sub-active: #40a9ff;
    --main-slider: #009ce0;
    --sub-slider: #91d5ff;
    --main-primary: #0093bd;
    --sub-primary: #00aad4;
    --main-secondary: #0b89c6;
    --sub-secondary: #0c8ac6;
    --main-tertiary: #5e85c9;
    --sub-tertiary: #5e85e9;
    --main-red-scale: #ff4d4f;
    --sub-red-scale: #ff6f6f;
    --main-blue-scale: #096dd9;
    --sub-blue-scale: #3b9dff;
    --main-select: #043468;
    --sub-select: #053e7b;

    --main-link-marker: #f93e3e;
    --sub-link-marker: #ff4f4f;
    --main-danger: #e95e5e;
    --sub-danger: #ff6f6f;
    --main-warning: #ffb300;
    --sub-warning: #ffd300;
    --main-info: #3a9eff;
    --sub-info: #3b9dff;
    --main-disabled: #8aaeaf;
    --sub-disabled: #6b727e;

    --main-online: #3ec93e;
    --sub-online: #4fcf4f;

    --gradient-diagonal-stripe: repeating-linear-gradient(
        45deg,
        #44444466,
        #44444466 8px,
        #00000000 8px,
        #00000000 12px
    );

    // Margin / Padding
    --spacing-px: 1px;
    --spacing-xxs: 2px;
    --spacing-xs: 5px;
    --spacing-sm: 7px;
    --spacing: 10px;
    --spacing-lg: 15px;
    --spacing-xl: 18px;
    --spacing-2xl: 20px;
    --spacing-3xl: 30px;
    --spacing-antd: 11px;

    // Font
    --ff-ocg: "DFHSGothic-W3-WIN-RKSJ-H";
    --fs-xs: 9px;
    --fs-sm: 12px;
    --fs: 14px;
    --fs-lg: 18px;
    --fs-xl: 20px;
    --fs-2xl: 24px;
    --fs-3xl: 30px;
    --fs-4xl: 40px;
    --fs-ocg: 15px;
    --fs-thumb: 13px;

    // Border Radius
    --br-sm: 1px;
    --br: 2px;
    --br-lg: 4px;
    --br-xl: 8px;

    // Border Width / Outline Width
    --bw: 1px;
    --bw-lg: 2px;
    --bw-xl: 4px;

    // Box Shadow
    --bs-1: 0 0 0 1px #2a2a2a;
    --bs-2: 0 0 4px 2px #111111;
    --bs-button: 0 0 2px 0 #333333;
    --bs-block: 0 0 1px 1px #2a2a2a;
    // --bs-input: 0 0 2px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.24);
    --bs-input: none;

    // Text Shadow
    --ts: 0 1px #222;

    // Size
    --width-label: 105px;

    h1 {
        color: var(--color-heavy);
        font-size: var(--fs-2xl);
    }
    h2 {
        line-height: 1;
        font-size: var(--fs-lg);
        color: var(--color-vendor);
        padding: var(--spacing-sm) 0;
        .label {
            color: var(--color-vendor);
        }
        &:first-child {
            padding-top: 0;
        }
    }
    h3 {
        color: var(--color);
    }

    ul {
        padding-inline-start: var(--spacing-2xl);
        margin-block-end: 0;
        li {
            line-height: 1.5;
        }
    }

    blockquote {
        background: var(--main-level-4);
        border-left: var(--bw-xl) solid var(--sub-level-4);
        margin: 0;
        padding: var(--spacing-xs) var(--spacing-sm);
    }

    .center {
        text-align: center;
    }
    .truncate {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
}
hr {
    margin: 0;
    border: none;
    border-top: var(--bw) solid var(--sub-level-1);
}
#app {
    width: 100%;
    min-height: 100vh;
    padding: var(--spacing) 0;
    overflow-y: scroll;
    position: relative;
}
.manager_visible {
    .app-container {
        margin-right: 280px;
    }
}
.app-container {
    display: flex;
    max-width: 1600px;
    background-color: #484848;
    box-shadow: 0 0 2px 2px #202020;
    margin: auto;
    border-radius: var(--br-xl);
    &.app-container-export {
        flex-direction: row-reverse;
        > .card-filter-panel {
            display: none;
        }
        > .card-info-panel {
            display: none;
        }
    }
    .card-filter-panel {
        flex: 0 0 0;
    }
    .card-info-panel {
        flex: 3;
        padding: var(--spacing);
        color: var(--color-heavy);
        text-shadow: var(--ts);
        position: relative;
    }
    .card-preview-panel {
        flex: 0 0 580px;
        position: relative;
        overflow: hidden;
        &.export-tainted {
            .data-button-panel {
                color: var(--main-danger);
            }
        }
        &.export-normal {
            .data-button-panel {
                color: var(--main-info);
            }
        }
        #save-button-tainted {
            text-align: center;
            justify-content: space-around;
        }
        .preview-sliding-window {
            padding-bottom: var(--spacing-sm);
            border-radius: var(--br-xl);
        }
        &.sliding-type_manual {
            #preview-canvas.js-export-available,
            .preview-sliding-window {
                cursor: grab;
            }
            canvas {
                cursor: grab !important;
            }
        }
        &.sliding-type_auto {
            .preview-sliding-window {
                transition: transform 0.5s ease-in-out;
            }
        }
    }
}

.control-board {
    .control-zoom {
        display: grid;
        grid-template-columns: var(--card-width) 1fr;
        > span {
            display: flex;
            align-self: center;
            margin-left: var(--spacing-xs);
        }
    }
}

.hidden {
    visibility: hidden;
}
.input-ocg {
    input,
    textarea.ant-input {
        font-size: var(--fs-ocg);
        line-height: 1.4666;
        font-family: var(--ff-ocg);
    }
}

/** Avoid annoying user agent style, should have use css reset? */
input:-internal-autofill-selected {
    background-color: unset !important;
}

:focus-visible {
    outline: 2px solid var(--focus);
}

/** Custom Scrollbar **/
/* width */
::-webkit-scrollbar {
    width: var(--spacing);
}
/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}
/* Handle */
::-webkit-scrollbar-thumb {
    background: #666677;
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555566;
}

/** Slow animation for download button **/
.data-button-panel .button-label {
    background: linear-gradient(135deg, #22c1c3 0%, #298ae9 50%, #22c1c3 100%);
    background-size: 300% 300%;
    animation: SlowPulsating ease infinite 10s;
    &:hover {
        background: linear-gradient(90deg, #32d1d3 0%, #399af9 50%, #32d1d3 100%);
    }
}
@keyframes SlowPulsating {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

.card-lightbox-overlay {
    .ant-modal {
        max-width: unset;
    }
    .ant-modal-close-x {
        display: none;
    }
    .ant-modal-content .ant-modal-body {
        --lightbox-padding: var(--spacing-lg);
        padding: var(--lightbox-padding);
        width: calc(var(--card-width) + var(--lightbox-padding) * 2);
    }
}

#sentry-feedback {
    --button-primary-background: var(--main-active);
    --button-primary-hover-background: var(--sub-active);
    --dialog-border-radius: var(--br-xl);
    --dialog-padding: var(--spacing);
    --input-focus-outline: var(--bw) auto var(--main-active);
}

.sliding-movable-window {
    .moveable-line,
    .moveable-control {
        visibility: hidden;
    }
}
